/*Colores y Generales*/
:root {
  --dark-green: #257251;
  --Very-Dark-Blue-alpha: hsl(229, 31%, 21%, 0.8);
  --dark-green-check: #1b5732;
  --dark-font-blue: #253363;
  --dark-font-blue-alternative: #1b273d;
  --grey-font: #A59E9E;
  --padding-container: 60px 0;
}

body {
  font-family: "Open Sans", sans-serif;
}

.container {
  width: 90%;
  max-width: 1200px;
  overflow: hidden;
  margin: 0 auto;
  padding: var(--padding-container);
}
/*Subrayado para ciertos titulos*/
.subline {
    position: relative;
    display: inline-block;
    padding-bottom: 12px;
  }
  
  .subline:before {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 100%;
    height: 5px;
    background-color: var(--dark-green);
    transform: skew(-20deg);
    z-index: -1;
  }

/*Generales Modal*/

.hideModal{
  display: none;
}
  /*Generales para Admin*/
  .nav__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 0px solid black;
  background: rgba(37, 51, 99,0.98);
  height: 80px;
  padding-right: 30px;

  position: fixed;
  width: 100%;
  z-index: 100;
}

.nav__top-left {
  background-color: var(--dark-green);
  height: 100%;

  display: flex;
  align-items: center;
  text-align: center;
}

.nav__top-right {
  color: #fff;
}

.nav__top-right a {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  padding: 15px 24px 15px 24px;
  background-color: var(--dark-green);
  border-radius: 4px;
}

.navbar-toggle {
  display: none;
}

.nav__adm {
  display: block;
  width: 260px;
  color: #fff;
  text-decoration: none;
}

/*Nav__Side*/

.nav__side {
  position: fixed;
  width: 260px;
  background-color: var(--dark-font-blue);
  top: 0;
  bottom: 0;
}

.nav__side-option {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding-inline-start: 0;
  gap: 30px;
 }

.active-menu {
  background-color: var(--dark-green) !important;
}

.nav__side-option a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #fff;
  padding: 30px 15px;
}

.nav__side-option a i {
  margin-right: 15px;
}

.option__img {
  display: block;
  text-align: center;
}

.user-image {
  margin: 25px auto;
}

.page__main {
  margin: 0 0 0 260px;
  padding: 15px 30px;
  min-height: 700px;
  background: #f3f3f3;
}

.page__inner {
  background-color: #fff;
  padding: 30px;
}

.inner__header {
  margin-bottom: 30px;
}

